@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400|Raleway:400,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
  width: 100%;  
}

/* * {
  margin: 0 !important;
  padding: 0 !important;
} */

.header {
  background-image: url(/fylo-p1/static/media/bg-intro.6d87f674.svg);
  height: 445px;
  width: 375px;
}

.illustration {
  height: 240px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  top: 90px;
}

.logo {
  -webkit-text-decoration-color: white;
          text-decoration-color: white;
  width: 90px;
  /* right: -40px;
  height: 80px; */

}

.contact {
  /* justify-content: center; */
  position: relative;
  top: 40px;
  padding-left: 40px;
  width: 375px;
}

.header a {
  color: white;
  font-size: 14px;
  padding-right: 10px;
  padding-left: 10px;
}

.header .icons {
  padding-left: 45px;
}


@media screen and (min-width: 376px) {
  .header {
    height: 600px;
    width: 1440px;
    background-image: none;
  }
}

.descContainer {
  display: block;
  margin-left: 0;
  margin-right: 0;
}

/* Typography */

.descColH {
  font-family: 'Raleway';
  color: hsl(243, 87%, 12%);
}

.descColp p {
  font-family: 'Open Sans';
  font-weight: 400;
  font-size: 12px;
  color: hsl(0, 0%, 50%);
  padding-top: 5px;
}

.signUpGrp input {
  width: 265px;
}

.pp {
  color: hsl(243, 86%, 85%);
  text-decoration: underline;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 13px;
}

.colorMe1 {
  background-color: hsl(163, 95%, 43%) !important;
  border-color: hsl(163, 95%, 43%) !important;
  width: 130px;
}

.callToA {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  color: hsl(243, 86%, 85%);
  font-size: 15px;
}


